// import react-three-fiber
import { Canvas, useFrame, useLoader, extend } from "react-three-fiber"

//@ts-ignore
import { TextureLoader } from 'three/src/loaders/TextureLoader';
//@ts-ignore
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
//@ts-ignore
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';


import Architect from "../components/architect";
import "./canvas.css";

import patternOne from "../images/textures/Metal034_1K-PNG/Metal034_1K_Color.png";
import fontRobotoRegular from "./Roboto_Regular.json";
import fontGentilisRegular from "three/examples/fonts/gentilis_regular.typeface.json";
import fontHelivetikerRegular from "three/examples/fonts/helvetiker_regular.typeface.json";
import fontOptimerRegular from "three/examples/fonts/optimer_regular.typeface.json";


extend({ FontLoader })
extend({ TextGeometry })

function TextMesh(props: any) {
  const [hovered, setHover] = useState(false);
  const [active, setActive] = useState(false)

  const mesh: Mesh = useRef();

  // actions to perform in current frame
  useFrame(() => {
    if (!hovered) {
      //mesh.current.rotation.x = mesh.current.rotation.y += 0.01;
      mesh.current.rotation.y += 0.01;
    }
    mesh.current.geometry.center();
  });

  // create lava texture
  // const lava_texture = useLoader(THREE.TextureLoader, lava);
  // lava_texture.wrapS = THREE.RepeatWrapping;
  // lava_texture.wrapT = THREE.RepeatWrapping;
  // lava_texture.repeat.set(0.05, 0.05);

  // // create texture
  const patternOneTexture = useLoader(TextureLoader, patternOne);
  patternOneTexture.wrapS = RepeatWrapping;
  patternOneTexture.wrapT = RepeatWrapping;
  patternOneTexture.repeat.set(1, 1);

  // load in font
  //const font = new FontLoader().parse(fontRobotoRegular);
  const font = new FontLoader().parse(fontOptimerRegular);

  // configure font mesh
  const textOptions = {
    font,
    size: 2.7,
    height: active ? 0 : 1
  };

  const getTexture = () => {
    const textureMap: any = {
      //lava: lava_texture,
      //carbonFiber: cf_texture,
      patternOne: patternOneTexture,
    };

    return textureMap[props.texture];
  };

  // return <mesh position={[0,0,0]}>
  //   <textGeometry args={['Excellent', {font, size:5, height: 1}]}/>
  //   <meshLambertMaterial attach='material' color={'gold'}/>
  // </mesh>;

  return (
    <mesh
      {...props}
      ref={mesh}
      position={[0,0,-2]}
      onClick={e => setActive(!active)}
      onPointerOver={(e) => setHover(true)}
      onPointerOut={(e) => setHover(false)}
    >
      <textGeometry attach="geometry" args={['Sheriff', textOptions]} />
      <meshStandardMaterial attach="material" color="gold" args={[{ map: getTexture() }]}  />
    </mesh>
  );
}


const Box = (props: any) => {

  // This reference will give us direct access to the mesh so we can animate it
  const mesh: Mesh = useRef();

  // Set up state for the hovered and active state
  const [hovered, setHover] = useState(false)
  const [active, setActive] = useState(false)

  if(props.autoRotate) {
    // Rotate mesh every frame, this is outside of React without overhead
    useFrame((state, delta) => {
      if (mesh.current && mesh.current.rotation) {
        mesh.current.rotation.x = mesh.current.rotation.y += 0.01
      }
    })
  }

  return (
    <mesh
        {...props}
        ref={mesh}
        scale={active ? [1.5, 1.5, 1.5] : [1, 1, 1]}
        onClick={e => setActive(!active)}
        onPointerOver={e => setHover(true)}
        onPointerOut={e => setHover(false)}
    >
      <boxGeometry attach="geometry" args={[1, 1, 1]} />
      <meshStandardMaterial
        attach="material"
        color={hovered ? "green" : "orange"}
        />
    </mesh>
  )
};

const doubleSide = DoubleSide
const deg = MathUtils.degToRad
const colors = ['#21242d', '#ea5158', '#0d4663', '#ffbcb7', '#2d4a3e', '#8bd8d2']

const Scene = ({size, elevation}: {size: number, elevation: number}) => {
    const color = 'blue';

    return <mesh scale={[size, size, 1]} rotation={[deg(-90), 0, 0]} position={[0, elevation, 0,]}>
        <planeGeometry attach="geometry" args={[1, 1]} />
        <meshPhongMaterial attach="material" color={color} depthTest={false} />
    </mesh>
}


